.resultfortv {
  width: 100%;
  height: 100vh;
  display: flex;
  overflow-y: scroll;
  overflow-x: hidden;
  position: fixed;
  color: white;
  top: 0px;
  flex-direction: column;
  background-color: rgb(3, 14, 61);
}
.button {
  color: white;
  width: 50px;
  height: 50px;
  padding: 20px;
  margin-top: 20px;
}
.button:hover {
  cursor: pointer;
  color: blue;
}
.resultfortv__innerspace {
  width: 192vh;
  max-width: 100%;
  margin-left: 50px;
  margin-top: 40px;
  height: 90vh;
  display: flex;
  background-color: rgb(3, 14, 61);
}
.resultfortv__image {
  width: 90vh;
  height: 90vh;
  object-fit: contain;
}
.resultfortv__image > img {
  width: 60vh;
  margin-left: 5px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  height: 90vh;
}
.button__div {
  width: auto;
  color: rgb(0, 255, 76);
  height: 10vh;
  margin-left: 10px;
  background-color: rgba(0, 0, 0, 0.397);
  margin-top: -20vh;
}
.button__div > .event {
  width: 20vh;
  padding: 12px;
  margin-left: 15vh;
  text-align: center;
}
.event:hover {
  cursor: pointer;
}
.resultfortv__info {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 90vh;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: rgb(0, 0, 0, 0.2);
  box-shadow: 0 0 100px 1px rgba(250, 250, 250, 0.2);
  overflow-y: scroll;
}
.resultfortv__originaltitle {
  width: auto;
  margin-left: 2vh;
  margin-top: -30px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  font-size: 4vh;
  color: rgb(0, 255, 98);
  font-family: sans-serif;
}

.resultfortv__overview {
  margin-left: 2vh;
  margin-top: -20px;
  font-size: 20px;
}
.resultfortv__genre_production {
  margin-left: 2vh;
  margin-top: -20px;
}
.resultfortv__createdByTitle {
  font-size: 4vh;
  margin-bottom: 2vh;
  color: rgb(0, 255, 115);
}
.resultfortv__createdBy {
  text-indent: 2vh;
  font-size: 3vh;
  margin-bottom: 2vh;
}
.resultfortv__genre_title {
  font-size: 4vh;
  margin-bottom: 2vh;
  color: rgb(0, 255, 115);
}
.resultfortv__gernerlist {
  text-indent: 2vh;
  font-size: 3vh;
  margin-bottom: 2vh;
}
.resultfortv__production_title {
  font-size: 4vh;
  margin-bottom: 2vh;
  color: rgb(0, 255, 115);
}
.resultfortv__productionlist {
  text-indent: 2vh;
  font-size: 3vh;
  margin-bottom: 2vh;
}
.resultfortv__additonalInfo {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-left: 2vh;
  margin-bottom: 20px;
}

.resultfortv__additional1 {
  flex: 0.5;
}
.resutfortv__additional2 {
  flex: 0.5;
  margin-left: 20px;
}
.resultfortv__title {
  font-size: 4vh;
  margin-bottom: 2vh;
  color: rgb(0, 255, 115);
}
.resultfortv__result {
  font-size: 3vh;
  margin-bottom: 2vh;
  text-indent: 2vh;
}
.season__button {
  background-color: rgb(1, 1, 235);
  margin-left: 50px;
  margin-top: -10vh;
}

.season__button:hover {
  background-color: rgb(0, 255, 255);
}
